    border-bottom: 1px solid rgb(200, 200, 200);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.helpers-title {
    padding-left: 12px;
    margin: 0;
    letter-spacing: .12rem;
}

.helpers-title-row i {
    font-size: 1rem;
    color: rgb(150, 150, 150);
    padding: 10px;
    border-radius: 5px;
    -webkit-transition: all 300ms ease-out;
