        align-items: center;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}

.helpers-list-item:hover {
    cursor: pointer;
    background: rgba(250, 250, 250, .6);
    -webkit-box-shadow: 0px 0px 10px 0px lightgrey;
            box-shadow: 0px 0px 10px 0px lightgrey;
    -webkit-transform: scaleX(1.006);
        -ms-transform: scaleX(1.006);
            transform: scaleX(1.006);
}

.avatar-placeholder {
    width: 140px;
    height: 140px;
    border-radius: 50%;
    background: rgb(180, 180, 180);
    -webkit-box-shadow: 0px 0px 3px 1px lightgrey;
            box-shadow: 0px 0px 3px 1px lightgrey;
}

.helpers-list-item span h3 {
