.helper-list-item {
    min-width: 200px;
    min-height: 250px;
    background-color: rgba(150, 150, 150, 0.2);
    border-radius: 10px;
    margin: 0 10px;
    padding: 15px;
    -webkit-transition: all 300ms ease-out;
    -o-transition: all 300ms ease-out;
    transition: all 300ms ease-out;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.helper-list-item:hover {
    cursor: pointer;
    -webkit-transform: translateY(-1px);
        -ms-transform: translateY(-1px);
            transform: translateY(-1px);
    -webkit-box-shadow: 2px 2px 20px -5px  lightgrey;
            box-shadow: 2px 2px 20px -5px  lightgrey;
}

.helper-list-item img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
}

.item-text-container {
    background: rgba(250, 250, 250, 1);
    border-radius: 60px;
    padding: 5px 20px;
}

.item-text-container * {
    text-align: center;
    font-weight: 500;
    font-size: .9rem;
    color: #444;
    margin: 0;
}


